@font-face {
    font-family: Comfortaa;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2");
}

/*
Josh's Custom CSS Reset
https://www.joshwcomeau.com/css/custom-css-reset/
*/
*, *::before, *::after {
    box-sizing: border-box;
}
* {
    margin: 0;
}
body {
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}
img, picture, video, canvas, svg {
    display: inline-block;
    max-width: 100%;
}
input, button, textarea, select {
    font: inherit;
}
p, h1, h2, h3, h4, h5, h6 {
    overflow-wrap: break-word;
    hyphens: auto;
}
p {
    text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
    text-wrap: balance;
}
button {
    line-height: 1;
}
button:hover {
    cursor: pointer;
}

/* Variables */
:root {
    --logo-size: 120px;
    --loader-size: 48px;
    --loader-width: 8px;
    --loader-primary-color: gray;
    --loader-secondary-color: lightgray;
    --condition-icon-size: 96px;
    --body-bg-color: #B1F0F7;
    --info-div-bg-color: #F5F0CD;
}

body {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: Comfortaa, sans-serif;
    background-color: var(--body-bg-color);
}

#logo {
    height: var(--logo-size);
    width: var(--logo-size);
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% center;
}

h1 {
    text-align: center;
    font-size: 3rem;
}

#input-wrapper {
    text-align: center;
    margin-bottom: 16px;
    display: flex;
    border: 1px solid gray;
    border-radius: 8px;
    font-size: 1rem;
    height: 48px;
    width: max-content;
} 

#input-wrapper > input {
    border-radius: 8px 0 0 8px;
    border: none;
    width: 300px;
}

#input-wrapper > input:focus {
    outline: none;
}

#input-wrapper:has(input:focus) {
    outline: none;
    border: 2px solid gray;
}

#input-wrapper > button {
    border-radius: 0 8px 8px 0;
    border: none;
    padding: 0px 8px;
}

#input-wrapper > button:hover {
    filter: brightness(90%);
}

#loader {
    display: none;
    border: var(--loader-width) solid var(--loader-secondary-color); 
    border-top: var(--loader-width) solid var(--loader-primary-color); 
    border-radius: 50%;
    width: var(--loader-size);
    height: var(--loader-size);
    animation: spin 2s linear infinite;
}
  
@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

#weather-info-wrapper,
#error {
    width: 376px;
    border: 0px solid black;
    border-radius: 8px;
    padding: 24px;
    background-color: var(--info-div-bg-color);
    box-shadow: 0px 4px 4px gray;
}

#weather-info-wrapper > div {
    text-align: center;
}

#location {
    font-size: 2rem;
    font-weight: bold;
}

#condition-icon-and-temperature-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 16px 0;
}

#condition-icon {
    height: var(--condition-icon-size); 
    width: var(--condition-icon-size);
    margin-right: 8px;
}

#temperature-wrapper {
    display: flex;
    align-items: start;
}

#temperature {
    font-size: 4.5rem;
    line-height: 1;
}

#temperature-buttons-wrapper {
    display: flex;
    justify-content: center;
}

#temperature-buttons-wrapper > button {
    border: none;
    background-color: transparent;
    color: gray;
}

#temperature-buttons-wrapper > button:disabled {
    color: black;
}

#condition {
    margin-bottom: 16px;
    font-size: 1.5rem;
}

#other-info-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#error {
    display: none;
    font-size: 1.5rem;
    word-wrap: normal;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA,UAAU;AACV;IACI,sBAAsB;IACtB,4DAAoD;AACxD;;AAEA;;;CAGC;AACD;IACI,sBAAsB;AAC1B;AACA;IACI,SAAS;AACb;AACA;IACI,gBAAgB;IAChB,mCAAmC;AACvC;AACA;IACI,qBAAqB;IACrB,eAAe;AACnB;AACA;IACI,aAAa;AACjB;AACA;IACI,yBAAyB;IACzB,aAAa;AACjB;AACA;IACI,iBAAiB;AACrB;AACA;IACI,kBAAkB;AACtB;AACA;IACI,cAAc;AAClB;AACA;IACI,eAAe;AACnB;;AAEA,cAAc;AACd;IACI,kBAAkB;IAClB,mBAAmB;IACnB,mBAAmB;IACnB,4BAA4B;IAC5B,mCAAmC;IACnC,2BAA2B;IAC3B,wBAAwB;IACxB,4BAA4B;AAChC;;AAEA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,kCAAkC;IAClC,sCAAsC;AAC1C;;AAEA;IACI,wBAAwB;IACxB,uBAAuB;IACvB,yDAAiD;IACjD,4BAA4B;IAC5B,wBAAwB;IACxB,+BAA+B;AACnC;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;IACnB,aAAa;IACb,sBAAsB;IACtB,kBAAkB;IAClB,eAAe;IACf,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,0BAA0B;IAC1B,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,0BAA0B;IAC1B,YAAY;IACZ,gBAAgB;AACpB;;AAEA;IACI,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,+DAA+D;IAC/D,iEAAiE;IACjE,kBAAkB;IAClB,yBAAyB;IACzB,0BAA0B;IAC1B,kCAAkC;AACtC;;AAEA;IACI,KAAK,uBAAuB,EAAE;IAC9B,OAAO,yBAAyB,EAAE;AACtC;;AAEA;;IAEI,YAAY;IACZ,uBAAuB;IACvB,kBAAkB;IAClB,aAAa;IACb,0CAA0C;IAC1C,4BAA4B;AAChC;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,kCAAkC;IAClC,iCAAiC;IACjC,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,YAAY;IACZ,6BAA6B;IAC7B,WAAW;AACf;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;AAC3B;;AAEA;IACI,aAAa;IACb,iBAAiB;IACjB,iBAAiB;AACrB","sourcesContent":["/* Fonts */\n@font-face {\n    font-family: Comfortaa;\n    src: url(\"./assets/Comfortaa.woff2\") format(\"woff2\");\n}\n\n/*\nJosh's Custom CSS Reset\nhttps://www.joshwcomeau.com/css/custom-css-reset/\n*/\n*, *::before, *::after {\n    box-sizing: bor